import { Component, OnInit } from '@angular/core';
import {MakeuserService} from '../../services/makeuser.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  user ;
  toggle : false;
  savedPosts = [];

  constructor(private authService:MakeuserService, private router:Router) { }

  ngOnInit() {
this.user = JSON.parse( localStorage.getItem('user'));
this.getUserSavedPosts()


  }
  keyupHandlerFunction($event){
    console.log($event)
  }
  getUserSavedPosts(){
    var i =0 ;
    this.authService.userSaved(this.user.id).subscribe(data =>{

      for( i ; i < data.length ; i++){

        this.authService.userPosts(data[i].postId).subscribe( res =>{
              this.savedPosts.push(res);

        })
        
      }
      
    })
    this.savedPosts.reverse();
    
  }

}


