import { Component,OnChanges ,  Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-pagintion',
  templateUrl: './pagintion.component.html',
  styleUrls: ['./pagintion.component.css']
})
export class PagintionComponent implements OnChanges {

  constructor() { }

  @Input() items = [];
	@Input('page-size') pageSize = 4;
	@Output('page-changed') pageChanged = new EventEmitter();
	pages: any[];
	currentPage; 

	ngOnChanges(){
        this.currentPage = 1;
        
		var pagesCount = (this.items.length / this.pageSize); 
		if( pagesCount %2 == 0){
			this.pages = [];
			for (var i = 1; i <= pagesCount; i++)
				this.pages.push(i);
			
		}else{
			this.pages = [];
			for (var i = 1; i <= (pagesCount+1); i++)
				this.pages.push(i);
		}
		
	}

	changePage(page){
		this.currentPage = page; 
		this.pageChanged.emit(page);
	}

	previous(){
		if (this.currentPage == 1)
			return;

		this.currentPage--;
		this.pageChanged.emit(this.currentPage);
	}

	next(){
		if (this.currentPage == this.pages.length)
			return; 
		
		this.currentPage++;
		this.pageChanged.emit(this.currentPage);
	}

}



