import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {tokenNotExpired} from 'angular2-jwt';

@Injectable()
export class MakeuserService {
  authToken: any;
  user: any; 

  constructor( private http:Http ) { }

   registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    return this.http.post('users/register', user,{headers: headers})
      .map(res => res.json());
  }
  authenticateUser(user){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
  
    return this.http.post('users/login', user,{headers: headers})
      .map(res => res.json());
  }
  getProfile(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
  
    
    return this.http.get( 'users/profile' ,{headers: headers})
      .map(res => res.json());
  }
     loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }


  storeUserData(token, user){
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }
    loggedIn(){
    return tokenNotExpired('id_token');
  }
  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }
  updateUser( user , _id){
     let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.post('users/update/'+ _id, user,{headers: headers})
      .map(res => res.json());
      

  }
    
    getUser(id){
          let headers = new Headers();
    headers.append('Content-Type','application/json');
  
    
    return this.http.get( 'users/get-user/'+id ,{headers: headers})
      .map(res => res.json());

    }
    userSaved(id){
              let headers = new Headers();
    headers.append('Content-Type','application/json');
  
    
    return this.http.get( 'users/user-saved/'+id ,{headers: headers})
      .map(res => res.json());

    }
    userPosts(id){
                    let headers = new Headers();
    headers.append('Content-Type','application/json');
  
    
    return this.http.get( 'post/user-saved-posts/'+id ,{headers: headers})
      .map(res => res.json());


    }


}



