import { SearchComponent } from './components/search/search.component';
import { CatogriesViewComponent } from './components/catogries-view/catogries-view.component';
import { adminGuard } from './guards/admin.guard';
import { loginAdminGuard} from './guards/login.admin';
import { LoginGuard } from './guards/login.guard';
import { LoginAdminComponent } from './admin/login-admin/login-admin.component';
import { PostShowComponent } from './components/post-show/post-show.component';
import { UpdataPostComponent } from './admin/updata-post/updata-post.component';
import { PanalComponent } from './admin/panal/panal.component';
import { RestPasswordComponent } from './components/rest-password/rest-password.component';
import { AddPhotoComponent } from './components/add-photo/add-photo.component';
import { UpdateComponent } from './components/update/update.component';
import { PostComponent } from './components/post/post.component';
import { ProfileComponent } from './components/profile/profile.component';

import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { Router , RouterModule} from '@angular/router';
import { AuthGuard} from './guards/user.guards'




export const appRouter = RouterModule.forRoot([
    {path : '' , component : HomeComponent},
    {path : 'register' , component : RegisterComponent , canActivate :[ LoginGuard]  },
    {path : 'login' , component : LoginComponent , canActivate :[ LoginGuard]  },
   {path : 'post/:id' , component : PostShowComponent},
   {path :'profile' , component : ProfileComponent , canActivate : [AuthGuard ]},
   {path:'update', component : UpdateComponent  , canActivate : [AuthGuard ]},
   {path : 'add-photo' , component : AddPhotoComponent  , canActivate : [ AuthGuard]},
   { path : 'rest-password' , component : RestPasswordComponent  , canActivate : [ AuthGuard]},
   {path : 'admin' , component : PanalComponent , canActivate : [ adminGuard]},
   {path : 'update-post/:id' , component : UpdataPostComponent},
   {path : 'be-admin' , component : LoginAdminComponent , canActivate : [ loginAdminGuard]},
   {path : 'catogrey/:id' , component : CatogriesViewComponent},
   {path : 'sreach/:id' , component : SearchComponent},
])


