import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { PostService } from './../../services/post.service';
import { Component, OnInit , Input } from '@angular/core';

@Component({
  selector: 'app-add-post',
  templateUrl: './add-post.component.html',
  styleUrls: ['./add-post.component.css']
})
export class AddPostComponent implements OnInit {
  @Input() view : boolean = false;
  title : String;
  tumble  :  String ;
  catagoris  = [] ;
  content ;
  Text ;
  catNum = [ "catagory 1" ]
  catArray = []
  i;

  constructor( private addpost : PostService ,
              private flashMessage : FlashMessagesService,
              private router : Router
  
  ) { }

  ngOnInit() {
    this.i = 2;
    
  }

  onSubmit(){
    const Post ={
      title : this.title,
      tumble : this . tumble,
      catagoris : this.catArray ,
      content : this.Text
      
    }
        this.addpost.registerUser(Post).subscribe(data => {
      if(data.sucess){
        this.flashMessage.show(' post is publish  ', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/']);
      } else {
        this.flashMessage.show(' post is not publish ', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/admin']);
      }
    });
    

    
  }
    keyupHandlerFunction(event){
 this.Text = event;
  }
  catNumInc(){
    this.catNum.push( "catagory "+this.i);
    this.inc()
  }
  inc(){
    this.i++;
  }

}



