import { ActivatedRoute } from '@angular/router';
import { PostService } from './../../services/post.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.css']
})
export class SideBarComponent implements OnInit {

  posts = [];
  searchnum = 0;
  sreach = '';
  postCatogries  =[];

  constructor(private postservice: PostService,
    private prames: ActivatedRoute) { }

  ngOnInit() {
    this.postCatogries = []
    this.getPostsCatogries();

  }
  searchValue(event) {
    this.postservice.getPost().subscribe(data => {
      var i = 0;
      this.sreach = event;
      this.posts = [];
      var dataPosts = data.posts;
      for (i; i < dataPosts.length; i++) {
        var title = dataPosts[i].title;
         var post = dataPosts[i];
        var yesSearch = title.search(event);
        if (yesSearch >= 0) {
          this.posts.push(post)

        }
      }
      this.searchnum = this.posts.length;
      

    })
  }
  getPostsCatogries() {
    var arrays = []
    var catagoris = [];
    var i = 0;

  
      this.postservice.getPost().subscribe(data => {
        var i = 0;

        var dataPosts = data.posts;
        for (i; i < dataPosts.length; i++) {
          var title = dataPosts[i].title;
          var post = dataPosts[i];
         
            arrays.push(post.catagoris);
          
        }
        catagoris = [].concat.apply([], arrays);
        this.checkCatogries(catagoris)



      })


    

  }
  checkCatogries(p) {
    var i = 0;
    var array ;
    array= [];
    for (i; i < p.length; i++) {
      if (i < p.length) {
        if (p[i] == p[i + 1]) {
          continue;
        } else {
          
          array.push(p[i]);
        }
      }
    }
    this.postCatogries = [];
    this.postCatogries = array;
  }

}



