import { User } from './../update/user';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MakeuserService } from './../../services/makeuser.service';
import { Component, OnInit } from '@angular/core';



@Component({
  selector: 'app-add-photo',
  templateUrl: './add-photo.component.html',
  styleUrls: ['./add-photo.component.css']
})
export class AddPhotoComponent implements OnInit {

    user = new User();
    name: String;
  username: String;
  email: String;
  password: String;
  image : string;
  id : String;
  isUser;
  constructor( private authService : MakeuserService,
              private  flashMessage: FlashMessagesService,
              private router : Router
        
    ) { }

    ngOnInit() {
    this.authService.getProfile().subscribe(profile => {
      
      this.user = profile.user;
      this.name = profile.user.name;
      this.email = profile.user.email;
      this.username = profile.user.username;
      this.password = profile.user.password;
      this.id = profile.user._id;
      this.image = profile.user.image; 

    },
    err => {
      console.log(err);
      return false;
    });
    
  }
  addPhoto(){
     const upadtedUser = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password,
      image : this.image
    }
      this.authService.updateUser(upadtedUser , this.id ).subscribe( data => {
      
      if(data.sucess){
        this.flashMessage.show('Your date is updated ', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/profile']);
        console.log(upadtedUser)
      } else {
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/add-photo']);
      }
    })
  }

}



