import { PostService } from './../../services/post.service';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit ,  } from '@angular/core';

@Component({
  selector: 'app-catogries-view',
  templateUrl: './catogries-view.component.html',
  styleUrls: ['./catogries-view.component.css']
})
export class CatogriesViewComponent implements OnInit {
  posts =[];
  header = '';

  constructor( private params : ActivatedRoute ,
    private postService : PostService) { }

  ngOnInit() {
    this.params.paramMap.subscribe( params =>{
      let cat = params.get('id'); 
      this.header = cat;
this.postService.getCatogriesPosts(cat).subscribe( data =>{
this.posts = data})    })

  }

}



