import { VaildService } from './../../services/vaild.service';
import { Component, OnInit } from '@angular/core';
import { User } from './../update/user';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { MakeuserService } from './../../services/makeuser.service';

@Component({
  selector: 'app-rest-password',
  templateUrl: './rest-password.component.html',
  styleUrls: ['./rest-password.component.css']
})
export class RestPasswordComponent implements OnInit {
     user = new User();
    name: String;
  username: String;
  email: String;
  password: String;
  image : string;
  id : String;
  cpassword : String;

  constructor(
              private validateService: VaildService,
               private authService : MakeuserService,
              private  flashMessage: FlashMessagesService,
              private router : Router) { }

  ngOnInit() { this.authService.getProfile().subscribe(profile => {
      
      this.user = profile.user;
      this.name = profile.user.name;
      this.email = profile.user.email;
      this.username = profile.user.username;
      
      this.id = profile.user._id;
      this.image = profile.user.image; 

    },
    err => {
      console.log(err);
      return false;
    });
  }
    addPhoto(){
     const upadtedUser = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password,
      image : this.image
    }
   
    // Validate Email
    if(!this.validateService.confirmPassword(this.password , this.cpassword)){
      this.flashMessage.show('password is not matched', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
      this.authService.updateUser(upadtedUser , this.id ).subscribe( data => {
      
      if(data.sucess){
        this.flashMessage.show('Your date is updated ', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/profile']);
        console.log(upadtedUser)
      } else {
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/add-photo']);
      }
    })
  }

}



