import { FlashMessagesService } from 'angular2-flash-messages';
import { PostService } from './../../services/post.service';
import { MakeuserService } from './../../services/makeuser.service';
import { Component, OnInit ,Input, Output ,EventEmitter  } from '@angular/core';
import { ActivatedRoute , Router} from '@angular/router';

@Component({
  selector: 'app-add-comment',
  templateUrl: './add-comment.component.html',
  styleUrls: ['./add-comment.component.css']
})
export class AddCommentComponent implements OnInit {
  content ;
  user ;
  isUser = false;

@Input() id;
@Output() added = new EventEmitter  ;

  constructor( private userService : MakeuserService,
                private  postService : PostService,
              private params : ActivatedRoute ,
            private router : Router,
          private FlashMessages : FlashMessagesService) { }

  ngOnInit() {
    
      this.user = JSON.parse(localStorage.getItem('user'));
      if(this.user){

        this.isUser = true;
    }
    
    

  }
  onComment(){
        const  comment = {
          userId : this.user.id,
          content : this.content,
          postId : this.id,
          date : new Date()
    }
       

           this.postService.AddComment( comment).subscribe( res =>{
            if(res.success){
                      this.FlashMessages.show('Your comment is add ', {cssClass: 'alert-success', timeout: 3000});
                      this.added.emit();
                      this.content = '';
                      
                    }else{
                      this.FlashMessages.show('Your comment is not add ', {cssClass: 'alert-danger', timeout: 3000})
                      
                      
                    }
                  });
        
  
  }
  

}



