import { AddCommentComponent } from './../components/add-comment/add-comment.component';
import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class PostService {

  constructor( private http:Http  ) { }
     registerUser(post){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    return this.http.post('admin/add-post', post ,{headers: headers})
      .map(res => res.json());
  }

    getPost(){
          let headers = new Headers();

      return this.http.get( 'admin/get-posts',{headers: headers}).map( res => res.json())
    }
    findPost( id){
         let headers = new Headers();

      return this.http.get( 'admin/get-post/' +id,{headers: headers}).map( res => res.json())

    }
    updateUser( id , post){
       let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.put('admin/update-post/'+ id, post,{headers: headers})
      .map(res => res.json());
    }
    deleteUser(id){
             let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.delete('admin/delete-post/'+ id,{headers: headers})
      .map(res => res.json());


    }
    AddComment( comment){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.post('post/add-comment/', comment,{headers: headers})
      .map(res => res.json());

    }
    getComments(id){
      let headers = new Headers();

      return this.http.get( 'post/get-comments/' +id,{headers: headers}).map( res => res.json())

    }
    incCommets(id){
      let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.put('post/inc-comment/'+id,{headers: headers})
      .map(res => res.json());

    }
    savePost(savePost){
        let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.post('post/savepost', savePost,{headers: headers})
      .map(res => res.json());

    }
    getSavedPost(id){
           let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.get('post/saved-number/'+id,{headers: headers})
      .map(res => res.json());

    }
    unSavePost(id){
            let headers = new Headers();
    headers.append('Content-Type','application/json');
    
    
    return this.http.delete('post/un-save/'+id,{headers: headers})
      .map(res => res.json());

    }
    getCatogriesPosts(id){
      let headers = new Headers();
headers.append('Content-Type','application/json');


return this.http.get('catogrey/'+id,{headers: headers})
 .map(res => res.json());

}
}



