import { MakeuserService } from './../../services/makeuser.service';
import { VaildService } from './../../services/vaild.service';

import { Component, OnInit } from '@angular/core';   


import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;
  cpassword: String;

  constructor(
    private validateService: VaildService,
    private flashMessage:FlashMessagesService,
    
    private router: Router,
    private makeuser : MakeuserService
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password
    }
  

    // Required Fields
    if(!this.validateService.validateRegister(user)){
      this.flashMessage.show('Please fill in all fields', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // Validate Email
    if(!this.validateService.validateEmail(user.email)){
      this.flashMessage.show('Please use a valid email', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
    // Validate Email
    if(!this.validateService.confirmPassword(user.password , this.cpassword)){
      this.flashMessage.show('password is not matched', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }
     // Register user
    this.makeuser.registerUser(user).subscribe(data => {
      if(data.sucess){
        this.flashMessage.show('You are now registered and can log in', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/login']);
      } else {
        this.flashMessage.show('Something went wrong', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/register']);
      }
    });

  
    



  }

}



