import { MakeuserService } from './../../services/makeuser.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { PostService } from './../../services/post.service';
import { Http  , } from '@angular/http';import { ActivatedRoute , Router} from '@angular/router';

import { Component, OnInit , OnChanges } from '@angular/core';

@Component({
  selector: 'app-post-show',
  templateUrl: './post-show.component.html',
  styleUrls: ['./post-show.component.css']
})
export class PostShowComponent implements OnInit {
  post;
    title : String;
  tumble  :  String ;
  catagoris : String ;
  content;
  _id;
  Comments ;
  comments =[] ;
  useres  = [] ;
  Commentnum :Number;
  Commentadd : boolean;
  user;
  savednum;
  userSaveIt = false;
  isUser = false;
  showComments = 7;
  itBig = true;
  itsamll = false;
  

  
  constructor(  private params : ActivatedRoute ,
                private postService : PostService,
               private router : Router ,
               private FlashMsg : FlashMessagesService,
              private makeuserService : MakeuserService) { }
  Reload(){
    this.comments = [];

    this.getcomments(this.comments)

     


  }

  ngOnInit() {
    this.getcomments(this.comments);
          this.user = JSON.parse(localStorage.getItem('user'));
          this.getSavednum();
          this.checkIfUserSaved();
          if(this.user){
            this.isUser = true;
          }
          

  
  }

  getcomments(c){
      var i ;
       this.params.paramMap.subscribe( params =>{
       let id = params.get('id'); 
       this._id = id;

      this.postService.findPost(id).subscribe( data => {
        
        this.post = data;
        this.title = data.post.title;
        this.tumble = data.post.tumble;
        this.catagoris = data.post.catagoris;
        this.content = data.post.content;

      

      
      });

      this.postService.getComments(id).subscribe(data =>{

       this.Comments = data;
        var array =[];
        this.Commentnum = data.length;
        this.showMoreComments(this.Commentnum);
   

        
        for( i = 0; i< data.length ; i++){

         if( i < data.length-1){
            if( data[i+1].userId === data[i].userId || undefined ){
             continue; 
          }

         }
          this.makeuserService.getUser( data[i].userId).subscribe( user =>{

           for ( i=0; i< this.Comments.length ; i++){

             if( user._id === this.Comments[i].userId){
               var Comment ={
                 userImage : user.image,
                 username : user.name,
                 date : this.Comments[i].date,
                 content : this.Comments[i].content
                 
               }
               c.push(Comment) ;             
             }
           }
           c.reverse();
          })
        }
      });

    });
  }


  save(){

    const savedPost = {
      postId : this._id,
      userId : this.user.id
      
    }
    this.postService.savePost( savedPost).subscribe( data =>{
      this.userSaveIt = true;
      this.getSavednum()
    })
  }


  getSavednum(){
    this.postService.getSavedPost(this._id).subscribe(data => {
      this.savednum = data.length;

   })
    
  }


  checkIfUserSaved(){
    
    var userId = this.user.id;
    var i = 0 ;
        this.postService.getSavedPost(this._id).subscribe(data => {

        for(i ; i< data.length ; i++){
          if( data[i].userId == userId){
            this.userSaveIt = true;
          }
         }


   })
 

  }
 unsave(){
    if(confirm( "you will un save this post are you sure !!")){
         this.postService.unSavePost(this.user.id).subscribe( data =>{
     this.userSaveIt = false;
     this.getSavednum()
   })
    }
 }
 showMoreComments( c ){
   this.showComments = this.showComments + 5;
   var numOfComments = this.Commentnum;
   if( c  <= this.showComments ){
    this.itBig = false;
   } 
   
    
 }


}



