import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
import { PostService } from './../../services/post.service';
import { Component, OnInit ,Input } from '@angular/core';

@Component({
  selector: 'app-post-view',
  templateUrl: './post-view.component.html',
  styleUrls: ['./post-view.component.css']
})
export class PostViewComponent implements OnInit {
  @Input() view : boolean = false;
  posts =[];

  constructor( private postview : PostService,
               private router : Router,
                private FlashMsg : FlashMessagesService ) { }

  ngOnInit() {

    this.postview.getPost().subscribe( data =>{
      if( data.sucess){
        this.posts = data.posts;
      }
    })
  }
  deletePost(user){
      if(confirm("Are you sure to delete ")) {
        this.postview.deleteUser(user._id).subscribe( data =>{
          if( data.sucess){
            this.FlashMsg.show(' post is deleted  ', {cssClass: 'alert-success', timeout: 3000});
            this.router.navigate(['/admin']);
            

          }else{
            this.FlashMsg.show(' post is not deleted   ', {cssClass: 'alert-danger', timeout: 3000});
          }
        })
  }
  }

}



