import { FlashMessagesService } from 'angular2-flash-messages';
import { PostService } from './../../services/post.service';
import { Http  , } from '@angular/http';import { ActivatedRoute , Router} from '@angular/router';

import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-updata-post',
  templateUrl: './updata-post.component.html',
  styleUrls: ['./updata-post.component.css']
})
export class UpdataPostComponent implements OnInit {
  post = {};
    title : String;
  tumble  :  String ;
  catagoris = [];
  catArray = [];
  content : String;
  constructor( private params : ActivatedRoute ,
                private postService : PostService,
               private router : Router ,
               private FlashMsg : FlashMessagesService) { }

  ngOnInit() {
       this.params.paramMap.subscribe( params =>{
       let id = params.get('id'); 

      this.postService.findPost(id).subscribe( data => {
        
        this.post = data;
        this.title = data.post.title;
        this.tumble = data.post.tumble;
        this.catagoris = data.post.catagoris;
        this.content = data.post.content;
        this.catArray = this.catagoris
        console.log(this.catagoris);
        
      

      
      });
      


    });
  
  }
  onUpdate(){
           this.params.paramMap.subscribe( params =>{
       let id = params.get('id');

       const post ={

        title : this.title,
        tumble : this.tumble,
        catagoris : this.catArray,
        content : this.content
       }

       this.postService.updateUser( id , post).subscribe( data =>{
         if(data.sucess){
           this.FlashMsg.show(' post is updated   ', {cssClass: 'alert-success', timeout: 3000});

           this.router.navigate(['/admin']);

         }else{
           this.FlashMsg.show(' post is not-updated   ', {cssClass: 'alert-danger', timeout: 3000});

           
           
         }
       })



    });


  }
  keyupHandlerFunction($event){
    this.content = $event

  }
  

}



