import { PostService } from './../../services/post.service';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  posts = [];
  postCatogries;
  savePosts = [];
  search = '';

  constructor(private prames: ActivatedRoute,
    private postService: PostService

  ) { }

  ngOnInit() {
    this.getPosts(this.posts);

    this.getPostsCatogries();

  }
  getPosts(p) {
    this.prames.paramMap.subscribe(pramas => {
      let search = pramas.get('id');
      this.search = search;
      this.postService.getPost().subscribe(data => {
        var i = 0;

        var dataPosts = data.posts;
        for (i; i < dataPosts.length; i++) {
          var title = dataPosts[i].title;
          var post = dataPosts[i];
          var yesSearch = title.search(search);
          if (yesSearch >= 0) {
            p.push(post)

          }
        }
        this.savePosts = p;


      })


    })
  }

  getPostsCatogries() {
    var arrays = []
    var catagoris = [];
    var i = 0;

    this.prames.paramMap.subscribe(pramas => {
      let search = pramas.get('id');
      this.postService.getPost().subscribe(data => {
        var i = 0;

        var dataPosts = data.posts;
        for (i; i < dataPosts.length; i++) {
          var title = dataPosts[i].title;
          var post = dataPosts[i];
          var yesSearch = title.search(search);
          if (yesSearch >= 0) {
            arrays.push(post.catagoris);
          }
        }
        catagoris = [].concat.apply([], arrays);
        this.checkCatogries(catagoris)



      })


    })

  }
  checkCatogries(p) {
    var i = 0;
    var array = [];
    for (i; i < p.length; i++) {
      if (i < p.length) {
        if (p[i] == p[i + 1]) {
          continue;
        } else {
          array.push(p[i]);
        }
      }
    }
    this.postCatogries = array;
  }
  filterPosts(filter) {
    var posts = this.savePosts;
    var arrat = [];
    var i = 0; 
    var z = 0; 
    if (filter == 'all') {
      arrat = this.savePosts;

    } else {
      
      for(i; i< posts.length ; i++){
        if(posts[i].catagoris.indexOf(filter) >=0 ){
          arrat.push(posts[i]);
        }

  
      }
     

    }
    this.posts = arrat;
  }

}




